import React from 'react';
import {render} from '@testing-library/react-native';
import Home from './Home';
import res from '../../SalesOrderSample.json';

describe('Home', () => {
  it('Render Home', () => {
    render(<Home />);
  });
  it('Show list of orders', () => {
    const {getAllByTestId} = render(<Home />);
    const orderComponent = getAllByTestId('orderComponent');
    expect(orderComponent.length).toEqual(res.orders.length);
  });
  it('Show Button to scan order QR Code', () => {
    const {getByTestId} = render(<Home />);
    const qrCodeButton = getByTestId('qrCodeButton');
    expect(qrCodeButton).not.toBeNull();
  });
});
